*
  padding: 0
  margin: 0
  border: 0
  // cursor: none !important

*,
*::before,
*::after
  box-sizing: border-box

a:focus-visible,
button:focus-visible
  outline: 2px solid var(--main-blue)

a
  color: inherit
  outline: none

aside,
nav,
footer,
header,
section
  display: block

html,
body
  height: 100%
  min-width: var(--min-width)  

body
  font-size: 16px
  line-height: 1
  font-family: var(--font-family)
  color: var(--dark-color)
  background-color: var(--main-white)
  font-weight: 400
  font-style: normal

input,
button,
textarea
  font-size: inherit
  font-family: var(--font-family)

button
  cursor: pointer
  background-color: transparent

a,
a:visited
  text-decoration: none

a:hover
  text-decoration: none

ul li
  list-style: none

img
  vertical-align: top

h1,
h2,
h3,
h4,
h5,
h6
  font-weight: inherit
  font-size: inherit




.wrapper
  width: 100%
  min-height: 100%
  overflow: hidden
  display: flex
  flex-direction: column

.main
  flex: 1 1 auto	  

  &__hero
    padding-top: 256px 

  &__subcategories
    padding: 56px 0   

.container
  max-width: var(--max-width-container)
  padding: 0 15px
  margin: 0 auto
  @media (max-width: var(--md3))
    padding: 0 10px

.show
  display: block

.hide
  display: none

.fade
  animation-name: fade
  animation-duration: 1.5s

@keyframes fade
  from
    opacity: 0.1
  to
    opacity: 1

.btn
  display: inline-flex
  align-items: center
  justify-content: center

.hidden
  overflow: hidden

