.subcategories
  min-height: 232px

  &__container

  &__slider

  &__wrapper

  &__slide

  &__item  
    display: flex
    flex-direction: column
    align-items: center  

  &__img
    display: block
    width: 120px
    flex: 0 0 120px
    border-radius: 100%    
    background-color: #F3F0ED
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    margin-bottom: 16px
    &.subcategories__img--1
      background-image: url('../img/subcategories/img-1.png')  
    &.subcategories__img--2
      background-image: url('../img/subcategories/img-2.png')  
    &.subcategories__img--3
      background-image: url('../img/subcategories/img-3.png')  
    &.subcategories__img--4
      background-image: url('../img/subcategories/img-4.png')  
    &.subcategories__img--5
      background-image: url('../img/subcategories/img-5.png')  
    &.subcategories__img--6
      background-image: url('../img/subcategories/img-6.png')  
    &.subcategories__img--7
      background-image: url('../img/subcategories/img-7.png')  
    &.subcategories__img--8
      background-image: url('../img/subcategories/img-8.png')  
    &.subcategories__img--9
      background-image: url('../img/subcategories/img-9.png')  

  &__name
    font-size: 16px
    font-weight: 500
    line-height: 120%
    letter-spacing: -0.16px




