:root {
  --font-family: "EuclidCircularA", sans-serif;
  --min-width: 320px;
  --max-width-container: 1254px;
  --md-xl: 1200px;
  --md-l: 992px;
  --md-m: 768px;
  --md-s: 480px;
  --dark-color:#333333;
  --main-blue: #0A4881;
  --light-gray: #B3B3B3;
  --border-color: #E4EBF1;
  --main-white: #FFF;
  --accent-dark-orange: #D44A1B;
  --accent-light-green: #7BBF3F;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid var(--main-blue);
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: var(--min-width);
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: var(--font-family);
  color: var(--dark-color);
  background-color: var(--main-white);
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: var(--font-family);
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__hero {
  padding-top: 256px;
}
.main__subcategories {
  padding: 56px 0;
}

.container {
  max-width: var(--max-width-container);
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: var(--md3)) {
  .container {
    padding: 0 10px;
  }
}

.show {
  display: block;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.hidden {
  overflow: hidden;
}

@font-face {
  font-family: "EuclidCircularA";
  src: url("../fonts/EuclidCircularA-Regular.woff2") format("woff2"), url("../EuclidCircularA-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "EuclidCircularA";
  src: url("../fonts/EuclidCircularA-Medium.woff2") format("woff2"), url("../EuclidCircularA-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
.header {
  min-height: 208px;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 500;
}
.header__special {
  display: block;
  text-align: center;
  background-color: var(--main-blue);
  color: var(--main-white);
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
  padding: 4px 0;
}
.header__container {
  background-color: var(--main-white);
}
.header__top {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--border-color);
}
.header__center {
  position: relative;
  display: flex;
  align-items: center;
  padding: 23px 0;
}
.top-header__location {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
}
@media (any-hover: hover) {
  .top-header__location:hover .top-header__city {
    color: var(--main-blue);
  }
}
.top-header__location svg {
  width: 16px;
  height: 16px;
  fill: var(--main-blue);
  margin-right: 4px;
}
.top-header__city {
  margin-right: 9px;
  transition: color 0.3s ease;
}
.top-header__quantity {
  position: relative;
  color: var(--light-gray);
  white-space: nowrap;
  margin-right: 32px;
}
.top-header__quantity::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -5px;
  transform: translateY(-50%);
  width: 2px;
  height: 2px;
  border-radius: 100%;
  background-color: var(--light-gray);
}
.top-header__menu {
  margin-right: 69px;
}
.top-header__list {
  display: flex;
  align-items: center;
}
.top-header__item:not(.top-header__item:last-child) {
  margin-right: 32px;
}
.top-header__item--submenu {
  position: relative;
}
.top-header__link {
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  line-height: 120%;
  white-space: nowrap;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .top-header__link:hover {
    color: var(--main-blue);
  }
  .top-header__link:hover span {
    color: var(--main-blue);
  }
  .top-header__link:hover svg {
    fill: var(--main-blue);
  }
}
.top-header__link--element {
  position: relative;
  padding-left: 20px;
}
.top-header__link--element::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 2px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: rgba(212, 74, 27, 0.2);
}
.top-header__link--element::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 6px;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: var(--accent-dark-orange);
}
.top-header__link--submenu {
  display: flex;
  align-items: center;
  line-height: 110%;
}
.top-header__link--submenu span {
  color: var(--light-gray);
  margin-right: 2px;
  transition: color 0.3s ease;
}
.top-header__link--submenu svg {
  width: 16px;
  height: 16px;
  fill: var(--light-gray);
  transition: fill 0.3s ease, transform 0.3s ease;
}
.top-header__link--submenu.active span {
  color: var(--main-blue);
}
.top-header__link--submenu.active svg {
  transform: rotate(-180deg);
  fill: var(--main-blue);
}
.top-header__submenu {
  top: 24px;
  left: 0;
}
.top-header__sublist {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 8px;
}
.top-header__subitem {
  width: 100%;
}
.top-header__subitem:not(.top-header__subitem:last-child) {
  margin-bottom: 8px;
}
.top-header__sublink {
  display: block;
  width: 100%;
  text-align: left;
  white-space: nowrap;
  padding: 5px 19px;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .top-header__sublink:hover {
    background-color: rgba(51, 51, 51, 0.08);
  }
}
.top-header__phones {
  position: relative;
  margin-right: 24px;
}
.top-header__phones-btn {
  display: flex;
  align-items: center;
}
.top-header__phones-btn.active span {
  color: var(--main-blue);
}
.top-header__phones-btn.active svg {
  transform: rotate(-180deg);
  fill: var(--main-blue);
}
@media (any-hover: hover) {
  .top-header__phones-btn:hover span {
    color: var(--main-blue);
  }
  .top-header__phones-btn:hover svg {
    fill: var(--main-blue);
  }
}
.top-header__phones-btn span {
  color: var(--dark-color);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  margin-right: 2px;
  transition: color 0.3s ease;
}
.top-header__phones-btn svg {
  width: 16px;
  height: 16px;
  fill: var(--dark-color);
  transition: fill 0.3s ease, transform 0.3s ease;
}
.top-header__callback {
  margin-left: auto;
  padding: 6px 15px;
  color: var(--main-blue);
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  border-radius: 999px;
  border: 1.5px solid rgba(10, 72, 129, 0.2);
  transition: background-color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .top-header__callback:hover {
    background-color: rgba(10, 72, 129, 0.2);
  }
}
.center-header__burger {
  display: none;
}
.center-header__logo {
  display: block;
  flex: 0 0 212px;
  margin-right: 24px;
}
.center-header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.center-header__catalog {
  margin-right: 8px;
}
.center-header__search {
  position: relative;
  margin-right: 8px;
}
.center-header__search.active {
  position: fixed;
  width: 100%;
  height: 100%;
}
.center-header__favorite, .center-header__compare, .center-header__cart {
  flex: 0 0 56px;
  height: 56px;
  margin-right: 8px;
  border-radius: 8px;
  background-color: rgba(51, 51, 51, 0.08);
  transition: background-color 0.3s ease;
}
.center-header__favorite svg, .center-header__compare svg, .center-header__cart svg {
  width: 24px;
  height: 24px;
  fill: var(--dark-color);
  transition: fill 0.3s ease;
}
@media (any-hover: hover) {
  .center-header__favorite:hover, .center-header__compare:hover, .center-header__cart:hover {
    background-color: rgba(51, 51, 51, 0.16);
  }
  .center-header__favorite:hover svg, .center-header__compare:hover svg, .center-header__cart:hover svg {
    fill: var(--main-blue);
  }
}
.center-header__cart {
  position: relative;
}
.center-header__cart span {
  position: absolute;
  top: 5px;
  right: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: 500;
  letter-spacing: -0.13px;
  width: 21px;
  height: 21px;
  border-radius: 100%;
  background-color: var(--main-blue);
  color: var(--main-white);
  border: 2px solid #efefef;
  transition: border-color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .center-header__cart:hover span {
    border-color: #dedede;
  }
}
.center-header__account {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  padding: 20px 32px;
  border-radius: 8px;
  background-color: rgba(51, 51, 51, 0.08);
  transition: background-color 0.3s ease;
}
@media (any-hover: hover) {
  .center-header__account:hover {
    background-color: rgba(51, 51, 51, 0.16);
  }
}

.catalog-header__btn {
  background-color: var(--main-blue);
  padding: 16px 32px 16px 25px;
  border-radius: 8px;
}
.catalog-header__btn span {
  color: var(--main-white);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
}
.catalog-header__btn.active .catalog-header__svg-open {
  display: none;
}
.catalog-header__btn.active .catalog-header__svg-close {
  display: block;
}
.catalog-header__svg-open, .catalog-header__svg-close {
  width: 24px;
  height: 24px;
  fill: var(--main-white);
  margin-right: 8px;
}
.catalog-header__svg-close {
  padding: 4px;
  display: none;
}
.catalog-header__wrapper {
  top: 89px;
  left: 0;
  display: flex;
  align-items: flex-start;
}
.catalog-header__left {
  flex: 0 0 224px;
  padding: 32px 0;
  background-color: #F5F3F1;
}
.catalog-header__right {
  padding: 40px 24px;
}
.catalog-header__tab-btn {
  width: 100%;
  text-align: left;
  padding: 16px;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
  transition: background-color 0.3s ease;
}
.catalog-header__tab-btn.active {
  background-color: var(--main-white);
}
@media (any-hover: hover) {
  .catalog-header__tab-btn:hover {
    background-color: var(--main-white);
  }
}
.catalog-header__body {
  display: grid;
  grid-template-columns: repeat(3, 205px);
  column-gap: 24px;
  row-gap: 40px;
}
.catalog-header__category {
  display: block;
  color: var(--light-gray);
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  line-height: 120%;
  margin-bottom: 16px;
}
.catalog-header__subitem:not(.catalog-header__subitem:last-child) {
  margin-bottom: 15px;
}
.catalog-header__sublink {
  font-size: 16px;
  line-height: 160%;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .catalog-header__sublink:hover {
    color: var(--main-blue);
  }
}

.search-header__form {
  width: 519px;
  min-height: 56px;
}
.search-header__btn-mobile {
  display: none;
}
.search-header__result {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  width: 100%;
  top: 66px;
  left: 0;
  padding: 24px;
  background-color: var(--main-white);
  border-radius: 8px;
  transition: opacity 0.4s ease, visibility 0.4s ease;
  z-index: 100;
}
.search-header__result.active {
  opacity: 1;
  visibility: visible;
}

.search-form {
  padding: 16px;
  display: flex;
  align-items: center;
  border: 2px solid rgba(51, 51, 51, 0.08);
  background-color: var(--main-white);
  border-radius: 8px;
}
.search-form:focus-within {
  border-color: var(--dark-color);
}
.search-form__btn {
  margin-right: 8px;
}
.search-form__btn--search svg {
  width: 24px;
  height: 24px;
  fill: var(--light-gray);
}
.search-form__btn--clear {
  display: none;
}
.search-form__btn--clear svg {
  width: 18px;
  height: 18px;
  fill: var(--light-gray);
}
.search-form__btn--clear.active {
  display: block;
}
.search-form__label {
  width: 100%;
}
.search-form__input {
  width: 100%;
  color: var(--dark-color);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  outline: none;
  padding-right: 8px;
}
.search-form__input::placeholder {
  color: var(--light-gray);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
}

.search-result__top {
  border-bottom: 1px solid rgba(51, 51, 51, 0.08);
  padding-bottom: 18px;
}
.search-result__bottom {
  padding: 16px 0;
}
.search-result__all {
  position: relative;
  margin-top: 24px;
  display: inline-block;
  font-size: 13px;
  line-height: 120%;
  letter-spacing: -0.13px;
  color: var(--main-blue);
}
.search-result__all::before {
  position: absolute;
  content: "";
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--main-blue);
}

.top-result__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.top-result__item {
  border-radius: 999px;
  background: rgba(51, 51, 51, 0.08);
