}
.center-result__title {
  display: block;
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
  margin-bottom: 16px;
}
.center-result__item:not(.center-result__item:last-child) {
  margin-bottom: 8px;
}
.center-result__link {
  font-size: 16px;
  line-height: 160%;
}

.bottom-result__title {
  display: block;
  font-size: 13px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.13px;
  margin-bottom: 16px;
}
.bottom-result__item {
  display: flex;
  align-items: flex-start;
