}
.bottom-result__item:not(.bottom-result__item:last-child) {
  margin-bottom: 12px;
}

.product-result__img {
  flex: 0 0 125px;
  height: 84px;
  margin-right: 8px;
}
.product-result__img--1 {
  background: url("../img/header/search-product-1.jpg") center/cover no-repeat;
}
.product-result__img--2 {
  background: url("../img/header/search-product-2.jpg") center/cover no-repeat;
}
.product-result__img--3 {
  background: url("../img/header/search-product-3.jpg") center/cover no-repeat;
}
.product-result__content {
  flex: 1 1 auto;
}
.product-result__name {
  display: block;
  max-width: 150px;
  font-size: 14px;
  line-height: 160%;
  margin-bottom: 17px;
}
.product-result__prices {
  display: flex;
  align-items: center;
}
.product-result__price {
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
}
.product-result__price--new {
  margin-right: 8px;
}
.product-result__price--old {
  color: var(--light-gray);
  font-weight: 400;
  text-decoration: line-through;
}
.product-result__btn {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: var(--main-blue);
}
.product-result__btn svg {
  flex: 0 0 24px;
  height: 24px;
  fill: var(--main-white);
}

.bottom-header__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.bottom-header__item--submenu {
  position: relative;
}
.bottom-header__link {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  border-radius: 8px;
  padding: 14px 16px 14px 24px;
