}
.bottom-header__link--submenu svg {
  width: 20px;
  height: 20px;
  fill: var(--dark-color);
  margin-left: 2px;
  transition: transform 0.3s ease, fill 0.3s ease;
}
.bottom-header__link--submenu.active {
  background-color: var(--main-blue);
