}
.bottom-header__link--new {
  color: var(--accent-dark-orange);
  padding: 14px 24px;
}
.bottom-header__link-element {
  position: relative;
  color: var(--accent-light-green);
  padding-left: 14px;
}
.bottom-header__link-element::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 6px;
  width: 3px;
  height: 3px;
  border-radius: 100%;
  background-color: var(--light-gray);
}
.bottom-header__wrapper {
  top: 87px;
  padding: 16px;
  display: grid;
  grid-template-columns: repeat(4, 210px);
  column-gap: 24px;
  row-gap: 40px;
}
.bottom-header__wrapper--left {
  left: 0;
}
.bottom-header__wrapper--center {
  left: 50%;
  transform: translateX(-50%);
}
.bottom-header__wrapper--right {
  right: -110px;
}
.bottom-header__category {
  display: block;
  color: var(--light-gray);
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -0.13px;
  line-height: 120%;
  margin-bottom: 16px;
}
.bottom-header__subitem:not(.bottom-header__subitem:last-child) {
  margin-bottom: 15px;
}
.bottom-header__sublink {
  font-size: 16px;
  line-height: 160%;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .bottom-header__sublink:hover {
    color: var(--main-blue);
  }
}

.header-submenu {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  border-radius: 6px;
  background-color: var(--main-white);
  box-shadow: 0px 16px 24px 0px #ECE5DF;
  transition: opacity 0.4s ease, visibility 0.4s ease;
  z-index: 5;
}
.header-submenu.active {
  opacity: 1;
  visibility: visible;
}

.hero {
  min-height: 586px;
}
.hero__sliders {
  display: flex;
}
.hero__slider {
  min-width: 0;
}
.hero__slider--left {
  position: relative;
  flex: 0 1 912px;
  margin-right: 24px;
}
.hero__slider--right {
  flex: 0 0 288px;
}

.slider-left__link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.slider-left__pagination.swiper-horizontal > .swiper-pagination-progressbar, .slider-left .swiper-pagination-progressbar.swiper-pagination-horizontal {
  position: relative;
  height: 2px;
  background-color: rgba(51, 51, 51, 0.08);
  top: 8px;
  left: 16px;
  width: 880px;
  z-index: 50;
}
.slider-left__btns {
  width: 100%;
}
.slider-left__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 48px;
  height: 48px;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 10;
  transition: background-color 0.3s ease;
}
.slider-left__btn svg {
  width: 24px;
  height: 24px;
  fill: var(--main-blue);
}
@media (any-hover: hover) {
  .slider-left__btn:hover {
    background-color: var(--main-white);
  }
}
.slider-left__btn--prev {
  left: 16px;
}
.slider-left__btn--next {
  right: 16px;
}
.slider-left__btn--next svg {
  transform: rotate(180deg);
}

.slider-right__body {
  width: 100%;
  height: 100%;
}
.slider-right__link {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px 24px 20px 24px;
  background: url("../img/hero/slide-2.jpg") center/cover no-repeat;
}
.slider-right__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--main-white);
  font-size: 11px;
  font-weight: 500;
  line-height: 140%;
}
.slider-right__date {
  display: flex;
  align-items: center;
  color: var(--light-gray);
  font-size: 13px;
  line-height: 120%;
  letter-spacing: -0.13px;
  margin-bottom: 8px;
}
.slider-right__day {
  position: relative;
  display: block;
  margin-right: 20px;
}
.slider-right__day::after {
  position: absolute;
  content: "";
  top: 50%;
  right: -12px;
  transform: translateY(-50%);
  width: 2px;
  height: 2px;
  border-radius: 100%;
  background-color: var(--light-gray);
}
.slider-right__time {
  display: block;
  padding-left: 20px;
  background-image: url("../img/hero/clock.svg");
  background-position: 0px 0px;
  background-repeat: no-repeat;
  background-size: 16px 16px;
}
.slider-right__descr {
  color: var(--main-white);
  font-size: 18px;
  font-weight: 500;
  line-height: 130%;
  letter-spacing: -0.18px;
}
.slider-right__pagination {
  position: relative;
}
.slider-right__pagination.swiper-horizontal > .swiper-pagination-progressbar, .slider-right .swiper-pagination-progressbar.swiper-pagination-horizontal {
  height: 2px;
  background-color: rgba(51, 51, 51, 0.08);
  top: 8px;
  left: 16px;
  width: 256px;
  z-index: 50;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  height: 2px;
  background-color: var(--main-blue);
  border-radius: 999px;
}

.subcategories {
  min-height: 232px;
}
.subcategories__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.subcategories__img {
  display: block;
  width: 120px;
  flex: 0 0 120px;
  border-radius: 100%;
  background-color: #F3F0ED;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 16px;
}
.subcategories__img.subcategories__img--1 {
  background-image: url("../img/subcategories/img-1.png");
}
.subcategories__img.subcategories__img--2 {
  background-image: url("../img/subcategories/img-2.png");
}
.subcategories__img.subcategories__img--3 {
  background-image: url("../img/subcategories/img-3.png");
}
.subcategories__img.subcategories__img--4 {
  background-image: url("../img/subcategories/img-4.png");
}
.subcategories__img.subcategories__img--5 {
  background-image: url("../img/subcategories/img-5.png");
}
.subcategories__img.subcategories__img--6 {
  background-image: url("../img/subcategories/img-6.png");
}
.subcategories__img.subcategories__img--7 {
  background-image: url("../img/subcategories/img-7.png");
}
.subcategories__img.subcategories__img--8 {
  background-image: url("../img/subcategories/img-8.png");
}
.subcategories__img.subcategories__img--9 {
  background-image: url("../img/subcategories/img-9.png");
}
.subcategories__name {
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
}

.promo__container {
  height: 100%;
}
.promo__body {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
}
.promo__item {
  min-height: 240px;
  padding: 24px 40px;
  display: flex;
  justify-content: flex-end;
}
.promo__item--left {
  background: url("../img/promo/img-left.jpg") center/cover no-repeat;
}
.promo__item--left .promo__link {
  color: var(--main-blue);
}
.promo__item--left .promo__link::before {
  background-color: var(--main-blue);
}
.promo__item--right {
  background: url("../img/promo/img-right.jpg") center/cover no-repeat;
  color: var(--main-white);
}
.promo__item--right .promo__link::before {
  background-color: var(--main-white);
}
.promo__content {
  max-width: 248px;
  display: flex;
  flex-direction: column;
}
.promo__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.24px;
  margin-bottom: 8px;
}
.promo__descr {
  font-size: 14px;
  line-height: 160%;
  margin-bottom: 44px;
  flex: 1 1 auto;
}
.promo__link {
  position: relative;
  display: inline;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.16px;
}
.promo__link::before {
  position: absolute;
  content: "";
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 1;
  visibility: visible;
  transition: opacity 0.4s ease 0s, visibility 0.4s ease 0s;
}